import React, { Component, Fragment } from "react";
import { instanceOf, func } from "prop-types";
import { ReactComponent as AND_SVG } from "../assets/images/AND.svg";
import { ReactComponent as NAND_SVG } from "../assets/images/NAND.svg";
import { ReactComponent as OR_SVG } from "../assets/images/OR.svg";
import { ReactComponent as NOR_SVG } from "../assets/images/NOR.svg";
import { ReactComponent as NOT_SVG } from "../assets/images/NOT.svg";
import { ReactComponent as XOR_SVG } from "../assets/images/XOR.svg";
import { ReactComponent as XNOR_SVG } from "../assets/images/XNOR.svg";
import GateClass, { AND, NAND, OR, NOR, NOT, XOR, XNOR } from "../Classes/Gate";
import ClickableNode from "./ClickableNode";


class Gate extends Component {
  constructor(props) {
    super(props);
    props.gate.componentPosition.x = 50;
    props.gate.componentPosition.y = 50;
    this.state = {
      x: 50,
      y: 50,
      moving: false,
      mousePrevX: 0,
      mousePrevY: 0
    };
  }

  render() {
    const {
      props: { gate, clickNode },
      state: { x, y, moving, mousePrevX, mousePrevY }
    } = this;
    let SVG;
    switch (gate.gateType) {
      case AND:
        SVG = AND_SVG;
        break;
      case NAND:
        SVG = NAND_SVG;
        break;
      case OR:
        SVG = OR_SVG;
        break;
      case NOR:
        SVG = NOR_SVG;
        break;
      case NOT:
        SVG = NOT_SVG;
        break;
      case XOR:
        SVG = XOR_SVG;
        break;
      case XNOR:
        SVG = XNOR_SVG;
        break;
      default:
        SVG = null;
    }

    return (
      <svg
        x={x}
        y={y}
        onMouseDown={() => {
          this.setState({ moving: true });
        }}
        onMouseUp={() => {
          this.setState({ moving: false });
        }}
        onMouseMove={({ clientX, clientY }) => {
          if (moving) {
            const pos = {
              x: x - (mousePrevX - clientX),
              y: y - (mousePrevY - clientY)
            };
            this.setState(pos);
            gate.componentPosition.x = pos.x;
            gate.componentPosition.y = pos.y;
          }
          this.setState({
            mousePrevY: clientY,
            mousePrevX: clientX
          });
        }}
      >
        {<SVG />}
        {gate.gateType === NOT ? (
          <ClickableNode
              cx={5}
              cy={25}
            onClick={() => {
              clickNode({ x: x + 5, y: y + 25 }, gate, 0);
            }}
          />
        ) : (
          <Fragment>
            <ClickableNode
              cx={5}
              cy={15}
              onClick={() => {
                clickNode({ x: x + 5, y: y + 15 }, gate, 0);
              }}
            />
            <ClickableNode
              cx={5}
              cy={35}
              onClick={() => {
                clickNode({ x: x + 5, y: y + 35 }, gate, 1);
              }}
            />
          </Fragment>
        )}
        <ClickableNode
          cx={85}
          cy={25}
          onClick={() => {
            clickNode({ x: x + 85, y: y + 25 }, gate, -1);
          }}
        />
        {moving ? (
          <rect
            width="90"
            height="50"
            fillOpacity={0}
            stroke="green"
            strokeWidth={3}
          />
        ) : null}
      </svg>
    );
  }
}

Gate.propTypes = {
  gate: instanceOf(GateClass).isRequired,
  clickNode: func.isRequired
};

export default Gate;
