import React, { Component } from "react";
import Button from "./Button";

const hints = [
  "Try out each gate and see what they do.",
  "Try to make a complex gate out of simple gates.",
  "Try to make some form of memory.",
  "Try to make a basic binary calculator."
];

class IdeasBox extends Component {
  constructor(props) {
    super(props);

    this.state = {
      i: 0
    };
  }

  next() {
    let i = this.state.i + 1;
    while (i > hints.length - 1) {
      i -= hints.length;
    }
    this.setState({ i: i });
  }

  prev() {
    let i = this.state.i - 1;
    while (i < 0) {
      i += hints.length;
    }
    this.setState({ i: i });
  }

  render() {
    return (
      <div
        style={{
          width: 1000,
          height: 75,
          border: "1px solid black",
          paddingTop: 10
        }}
      >
        <div
          style={{ display: "inline-block", textAlign: "center", width: "15%" }}
        >
          <Button onClick={() => this.prev()}>
            <p style={{ marginTop: 8 }}>&lt;</p>
          </Button>
        </div>
        <div
          style={{ display: "inline-block", textAlign: "center", width: "70%" }}
        >
          {hints[this.state.i]}
        </div>
        <div
          style={{ display: "inline-block", textAlign: "center", width: "15%" }}
        >
          <Button onClick={() => this.next()}>
            <p style={{ marginTop: 8 }}>&gt;</p>
          </Button>
        </div>
      </div>
    );
  }
}

export default IdeasBox;
