import GameNode from "./GameNode";

const AND = "AND";
const NAND = "NAND";
const NOR = "NOR";
const NOT = "NOT";
const OR = "OR";
const XNOR = "XNOR";
const XOR = "XOR";

/**
 * @property {Object} parents
 */
class Gate extends GameNode {
  /**
   * @param {int} type
   */
  constructor(type) {
    super();
    this.gateType = type;
    this.parents = [
      null,
      null
    ];
    this.componentPosition = {
      x: 0,
      y: 0
    };
  }

  /**
   * @return {boolean} the output
   */
  getOutput() {
    const a = this.outputOf(0);
    const b = this.outputOf(1);

    switch (this.gateType) {
      case NOT:
        return !a;
      case AND:
        return a && b;
      case NAND:
        return !(a && b);
      case OR:
        return a || b;
      case NOR:
        return !(a || b);
      case XOR:
        return a !== b;
      case XNOR:
        return a === b;
      default:
        return false;
    }
  }

  /**
   * @param {int} i
   * @returns {boolean}
   */
  outputOf(i) {
    return this.parents[i] !== null && this.parents[i].getOutput();
  }
}

export { AND, NAND, NOR, NOT, OR, XNOR, XOR };
export default Gate;
