import React, { Fragment } from "react";
import { node, func, string } from "prop-types";

const Button = ({ onClick, children, label }) => {
  return (
    <div
      style={{
        cursor: "pointer",
        textAlign: "center",
        fontSize: "18pt",
        marginTop: "5px",
        border: "2px outset black",
        borderRadius: "4px",
        marginLeft: "5px",
        width: "90px",
        height: "50px",
        userSelect: "none",
        display: "inline-block"
      }}
      onClick={onClick}
    >
      <Fragment>
        {children}
        <p style={{ textAlign: "center", marginTop: "-5px" }}>{label}</p>
      </Fragment>
    </div>
  );
};

Button.propTypes = {
  label: string,
  onClick: func,
  children: node.isRequired
};

Button.defaultProps = {
  onClick: () => {},
  label: ""
};

export default Button;
