import GameNode from "./GameNode";

class Light extends GameNode {
    constructor() {
        super();
        this.parents = [null];
        this.componentPosition = {
            x: null,
            y: null,
        }
    }

    /**
     * @returns {boolean}
     */
    getOutput() {
        return this.parents[0] !== null && this.parents[0].getOutput()
    }
}

export default Light;