import React from "react";
import { func, instanceOf, number } from "prop-types";
import { ReactComponent as SwitchOn } from "../assets/images/switch_on.svg";
import { ReactComponent as SwitchOff } from "../assets/images/switch_off.svg";
import SwitchClass from "../Classes/Switch";
import ClickableNode from "./ClickableNode";

const Switch = ({ x, y, sw, update, clickNode }) => (
  <svg
    onClick={() => {
      sw.toggle();
      update();
    }}
    x={x}
    y={y}
  >
    {(sw.componentPosition.x = x)}
    {(sw.componentPosition.y = y)}
    {sw.getOutput() ? <SwitchOn /> : <SwitchOff />}
    <ClickableNode
      cx={45}
      cy={25}
      onClick={e => {
        e.stopPropagation();
        clickNode({ x: x + 45, y: y + 25 }, sw, -1);
      }}
    />
  </svg>
);

Switch.propTypes = {
  update: func.isRequired,
  sw: instanceOf(SwitchClass).isRequired,
  x: number.isRequired,
  y: number.isRequired,
  clickNode: func.isRequired
};

export default Switch;
