import GameNode from './GameNode';

class Switch extends GameNode {
    constructor() {
        super();
        this.state = false;
        this.componentPosition = {
            x: null,
            y: null,
        }
    }

    toggle() {
        this.state = !this.state;
    }

    /**
     * @returns {boolean}
     */
    getOutput() {
        return this.state
    }
}

export default Switch;