import React from "react";
import { ReactComponent as LightOff } from "../assets/images/light_off.svg";
import { ReactComponent as LightOn } from "../assets/images/light_on.svg";
import LightObject from "../Classes/Light";
import {func, instanceOf, number} from "prop-types";
import ClickableNode from "./ClickableNode";

const Light = ({ light, x, y, clickNode }) => (
  <svg x={x} y={y}>
    {(light.componentPosition.x = x)}
    {(light.componentPosition.y = y)}
    {light.getOutput() ? <LightOn /> : <LightOff />}
    <ClickableNode
        cx={5}
        cy={25}
        onClick={() => {
          clickNode({ x: x + 5, y: y + 25 }, light, 0);
        }}
    />
  </svg>
);

Light.propTypes = {
  light: instanceOf(LightObject).isRequired,
  x: number.isRequired,
  y: number.isRequired,
  clickNode: func.isRequired,
};

export default Light;
